import '../css/List.css';
import { Link, useHistory } from 'react-router-dom';
import { useEffect, useState } from 'react';


export default function List({num}){
    
    
    //페이지값 전달_useHistory
    const param = useHistory();
    num(param.location.pathname);
    console.log({num})
    
    //jsony
    let [data,setData] = useState();

    useEffect(()=>{
        fetch('../data/work.json')
        .then(res=>res.json())
        .then(works=>{
            // console.log(works)
            setData(works);
        })
    },[])
   
    //pagescroll top
    function scrollPos(){
        window.scrollTo(0,0);
    }
    
    return(
    <>
    <section id="listp">
        <div id="listtop">
            <h1>PROJECT<br/>LIST</h1>
        </div>
        <ul id="listcon">
            {
                data && data.map((work,k)=>(
                <li key={k}>
                    <Link to={`/Project/${k}`} onClick={scrollPos}>
                            <h3>{work.title}</h3>
                            <figure>
                                <p>{work.alpha}</p>
                                <video autoPlay loop muted>
                                    <source src={`../img/${work.thum}`} type="video/mp4"/>
                                </video>
                            </figure>
                            <div>
                                <span>
                                    {work.summary}
                                </span>
                            </div>
                        </Link> 
                </li>
                
            ))
           }
        </ul>
        <div>
        </div>
    </section>
    </>
    );
}