import React from "react";
import {useHistory} from "react-router-dom";
import ReactTypingEffect from "react-typing-effect";

export default function Main({num}){
    //페이지값 전달_useHistory
    const param = useHistory(); 
    num(param.location.pathname);


    return(
    <>
        <section>
            <div id="main">
                <div id="maintext">
                    <img src="../img/name.svg" id="svgname" alt="img"/>
                    <h2 id="hello">It's my</h2>
                    <h3 id="typingsub">
                        <ReactTypingEffect 
                        text={["Portfolio!"]}
                        typingDelay={2500}
                        eraseDelay={20000}/>
                    </h3>
                </div>
                <div id="subtext">
                    <p>Who AM I ?<br/>
                        /<br/>
                        I am a developer<br/>who always does my best.
                    </p>
                    <svg class="cir" viewBox="0 0 1920 1080" xmlns="http://www.w3.org/2000/svg">
                        <path id="MyPath" fill="none" stroke="white" d="M1919.9,357.9c0,0-163.5-175.4-427.3-173s-594,185-614.3,614.3c-4.9,103.4,22.8,197.5,67.6,280.8"/>
                    </svg>
                </div>
                <div id="foottext">
                    <p>
                        I am a developer who creates<br/>various ideas that are full in my head<br/>with both hands.
                    </p>
                    <h3>2021.FRONTEND DEVELOPER PORTFOLIO</h3>
                </div>
            </div>
        </section>
    </>
    );
}