import './App.css';
import {HashRouter, Route, Switch, Link} from "react-router-dom";
import Main from './Page/Main';
import About from './Page/About';
import Project from './Page/Project';
import Introduce from './Page/Introduce';
import List from './Page/List';
import { FiInstagram } from "react-icons/fi";
import { FiMail } from "react-icons/fi";
import { FiMessageCircle } from "react-icons/fi";
import { BsGithub } from "react-icons/bs";

function App() {
  
  function scrollPos(pageName){
    let num=0;
  
    switch(pageName){ 
      case '/About' : num=1; break;
      case '/Introduce' : num=1; break;
      case '/List' : num=2; break;
      case '/Project' : num=2; break;
      default : num=0;
    }
    setTimeout(()=>{
      window.scrollTo(0,0);
      const headnav = document.querySelectorAll('#navlist li');
      headnav.forEach(function(menu,k){
        menu.classList.remove('active');
      });
      headnav[num].classList.add('active');
      
    },100)    
  }
  

  return (
    <HashRouter>
    <header>
      <div id="head">
        <div id="nav">
          <ul id="navlist">
            <li className="active" >
              <Link to="/">HOME</Link>
            </li>
            <li className="" >
              <Link to="/About">ABOUT</Link>
            </li>
            <li className="" >
              <Link to="/List">PROJECT</Link>
            </li>
          </ul>
        </div>
        <div id="navIcon">
          <ul>
            <li><a href="https://open.kakao.com/o/sc9pCzDd" target="blank" rel="noopener"> <FiMessageCircle size="1.2em"/></a></li>
            <li><a href="https://github.com/yyoonseo93" target="blank" rel="noopener"> <BsGithub size="1.2em"/> </a></li>
            <li><a href="https://www.instagram.com/yoons__u/" target="blank" rel="noopener"> <FiInstagram size="1.2em"/> </a></li>
            <li><a href="mailto:yyoonseo93@gmail.com" target="_parent" rel="noopener"> <FiMail size="1.2em"/></a></li>
          </ul>
        </div>
      </div>
    </header>
    <Switch>
      <Route exact path="/"> <Main num={scrollPos}/> </Route>
      <Route path="/About"> <About num={scrollPos}/> </Route>
      <Route path="/Introduce"> <Introduce num={scrollPos}/> </Route>
      <Route path="/List"> <List num={scrollPos}/> </Route>
      <Route path="/Project/:num"> <Project num2={scrollPos}/> </Route>
    </Switch>
    
    </HashRouter>
  );
}

export default App;
