import { useRef } from 'react';
export default function Photo(){
    let inum = 0;
    let imgData, inter;
    let myimg = useRef();

    fetch('./data/aboutimg.json')
    .then(res=>res.json())
    .then(aboutimg=>{
        imgData=aboutimg;
    })

    function photoChange(){
        console.log('a')

        inter = setInterval(()=>{
            myimg.current.src=imgData[++inum].img;
            if(inum === 12) inum = 0;
        },100)
    }
    function photoStop(){
        myimg.current.src=imgData[0].img;
        clearInterval(inter)
    }


    return(
        <>
            <img src="img/img0.svg" alt="aboutimg" ref={myimg} onMouseEnter={photoChange} onMouseLeave={photoStop}/>
        </>
    );
}