import '../css/Project.css';
import { useEffect, useState } from 'react';
import {useParams, Link, useHistory} from "react-router-dom";

export default function Project({num2}){
    // //페이지값 전달_useHistory
    const param = useHistory();
    num2('/Project');


    let [data,setData] = useState(0);
    let {num} = useParams();

    //json
    useEffect(()=>{
        fetch('../data/work.json')
        .then(res=>res.json())
        .then(works=>{
            // console.log(works)
            setData(works[num]);
        })
    },[num])

    function createMarkup() { 
        return {__html: data.contents}; 
    } 

    //pagescroll top
    function scrollPos(){
        window.scrollTo(0,0);
        
    }
    return(
        <>
       
        <section id="project">
            <div>
                <div id="promain">
                    <h1>PROJECT<br/><b>NO.0{data.num}</b></h1>
                    <div id="plist">
                        <Link to="/List" onClick={scrollPos}>
                            <span>c</span>
                            <span>l</span>
                            <span>i</span>
                            <span>c</span>
                            <span>k</span>
                            <span>_</span>
                            <span>l</span>
                            <span>i</span>
                            <span>s</span>
                            <span>t</span>
                            
                        </Link>
                        
                    </div>
                    <div id="pmain_detail">
                        <div id="ptext">
                            <div id="psite">
                                <span>
                                    <a href={data.homepage} target="blank">
                                        Go to site!
                                    </a>
                                </span>
                            </div>
                            <h3>{data.title}</h3>
                            <p>
                                {data.summary}
                            </p>
                        </div>
                        <div id="pvideo">
                            {data &&
                                <video autoPlay loop muted>
                                    <source src={`../img/${data.thum}`} type="video/mp4"/>
                                </video>
                            }
                        </div>
                        
                    </div>
                    
                </div>

                <div dangerouslySetInnerHTML={createMarkup()} />


        </div>
    </section>
        
        </>
    );
}